@import "../sass/mixins.scss";

//GLOBAL//

.wrapper {
  max-width: 1200px;
  display: flex;
  margin: 0 auto;
}

body {
  height: 100%;
  // @include pink-bg;
}

span {
  .light-para {
    @include light-para;
  }
}

//MAIN//

header {
  @include pink-bg;
  width: 100%;
  height: 80px;
  z-index: 100;

  .main-header {
    width: 100%;
    height: 100%;
    justify-content: space-between;
    align-items: center;
    padding: 10px;

    .logo {
      padding: 5px;
      @include dark-title;
      font-size: 24px;
    }
    .nav {
      nav {
        ul {
          display: flex;
          align-items: center;

          li {
            display: block;
            transition-duration: 0.5s;
            padding: 8px;
            align-items: center;
            border-radius: 10px;

            a {
              text-decoration: none;
              @include dark-para;
              font-weight: bold;

              svg {
                height: auto;
                width: 30px;
              }
            }
          }

          li:hover {
            cursor: pointer;
            @include blue-bg;
          }

          li ul {
            visibility: hidden;
            opacity: 0;
            position: absolute;
            transition: all 0.5s ease;
            @include blue-bg;
            display: none;
            margin-top: 10px;
            padding: 0px 8px 8px 8px;
            border-radius: 10%;
          }

          li:hover > ul,
          li ul:hover {
            visibility: visible;
            opacity: 1;
            display: block;
            @include pink-bg;
          }

          li ul li {
            clear: both;
            width: 100%;

            display: block;

            a {
              text-decoration: underline;
            }
          }
        }
      }
    }
  }
}

section {
  width: 100%;
  //background-color: blueviolet;
  margin-top: 10px;

  .header-banner {
    width: 100%;
    display: flex;
    //margin: 30px auto;
    //background-color: blue;
    justify-content: center;

    .color {
      width: 100%;
      display: flex;
      background-color: #cceaff;
      justify-content: center;
      border-radius: 94% 6% 97% 3% / 7% 94% 6% 93%;
    }

    .hb-img {
      margin: 20px 0 30px 0;
      display: flex;
      width: 90%;
      height: 600px;
      background-image: url(../img/pink-living-room-chair.jpg);
      background-repeat: no-repeat;
      background-position: cover;
      border-radius: 50px;
      align-items: center;
      justify-content: center;

      .text {
        display: flex;
        //background-color: #ffffff63;

        p {
          @include light-title;
          font-size: 50px;
          font-style: italic;
        }
      }
    }
  }
}

